    <div class=\"messaging\">\n            <div class=\"inbox_msg\">\n              <div class=\"inbox_people\">\n                <div class=\"headind_srch\">\n                  <div class=\"recent_heading mb-2\">\n                    <h4>E-LEARNING</h4>\n                  </div>\n                  <div class=\"input-group rounded\">\n                    <input\n                      type=\"search\"\n                      class=\"form-control rounded\"\n                      placeholder=\"Search\"\n                      aria-label=\"Search\"\n                      aria-describedby=\"search-addon\"\n                    />\n                    <span class=\"input-group-text border-0\" id=\"search-addon\">\n                      <i class=\"fas fa-search\"></i>\n                    </span>\n                  </div>\n                </div>\n                <div class=\"inbox_chat\">\n                  <div class=\"chat_list active_chat\">\n                    <div class=\"chat_people\">\n                      <div class=\"chat_img\">\n                        <img\n                          src=\"https://ptetutorials.com/images/user-profile.png\"\n                          alt=\"sunil\"\n                        />\n                      </div>\n                      <div class=\"chat_ib\">\n                        <h5>\n                          วิชาคอมพิวเตอร์พื้นฐาน 1 <span class=\"chat_date\">Dec 25</span>\n                        </h5>\n                        <p>\n                          maix: สวัสดีครับ!!\n                        </p>\n                      </div>\n                    </div>\n                  </div>\n                  <div class=\"chat_list\">\n                    <div class=\"chat_people\">\n                      <div class=\"chat_img\">\n                        <img\n                          src=\"https://ptetutorials.com/images/user-profile.png\"\n                          alt=\"sunil\"\n                        />\n                      </div>\n                      <div class=\"chat_ib\">\n                        <h5>\n                          วิชาภาษาอังกฤษพื้นฐาน 1 <span class=\"chat_date\">Dec 25</span>\n                        </h5>\n                        <p>\n                          maix: สวัสดีครับ!!\n                        </p>\n                      </div>\n                    </div>\n                  </div>\n                </div>\n              </div>\n              <div class=\"mesgs\">\n                <div class=\"msg_history\" id=\"scroll\">\n                  <div class=\"incoming_msg\">\n                    <div class=\"incoming_msg_img\">\n                      <img\n                        src=\"https://ptetutorials.com/images/user-profile.png\"\n                        alt=\"sunil\"\n                      />\n                    </div>\n                    <div class=\"received_msg\">\n                      <div class=\"received_withd_msg\">\n                        <span class=\"time_date\"> maix</span>\n                        <p>\n                          สวัสดรครับ\n                        </p>\n                        <span class=\"time_date\"> 11:01 AM | June 9</span>\n                      </div>\n                    </div>\n                  </div>\n                  <div class=\"outgoing_msg\" v-for=\"n in data\" :key=\"n.id\">\n                    <div class=\"sent_msg\">\n                      <span class=\"time_date\"> {{ n.userName }}</span>\n                      <p>{{ n.message }}</p>\n                      <span class=\"time_date\"> 11:01 AM | June 9</span>\n                    </div>\n                  </div>\n                </div>\n                <div class=\"type_msg\">\n                  <div class=\"input_msg_write\">\n                    <input\n                      v-model=\"messageText\"\n                      type=\"text\"\n                      class=\"write_msg\"\n                      placeholder=\"Type a message\"\n                      @keyup.enter=\"sendMessage\"\n                    />\n                    <button\n                      class=\"msg_send_btn\"\n                      type=\"button\"\n                      @click=\"sendMessage\"\n                    >\n                      <i class=\"fa fa-paper-plane-o\" aria-hidden=\"true\"></i>\n                    </button>\n                  </div>\n                </div>\n              </div>\n            </div>\n          </div>\n        </div>\n      </section>\n    </div>\n    <div id=\"footer\">\n      <FooterComponent />\n    </div>\n  </div>\n</template>\n\n<script>\nimport axios from \"axios\";\nimport HeaderComponent from \"../../components/HeaderComponent.vue\";\nimport FooterComponent from \"../../components/FooterComponent.vue\";\nexport default {\n  name: \"chat_messages\",\n  components: { HeaderComponent, FooterComponent },\n  props: {\n    mag: String,\n  },\n  data() {\n    return {\n      messageText: \"\",\n      data: [\n        {\n          message:\n            \"สวัสดีครับ\",\n          userName: \"userx\",\n        },\n      ],\n    };\n  },\n  mounted() {},\n  methods: {\n    sendMessage() {\n      if (this.messageText != \"\") {\n        this.data.push({\n          message: this.messageText,\n          userName: \"userx\",\n        });\n        console.log(this.messageText);\n        this.messageText = \"\";\n      }\n      var scroll = this.$el.querySelector(\"#scroll\");\n      scroll.scrollTop = scroll.scrollHeight;\n    },\n  },\n};\n</script>\n\n<style>\n.container {\n  max-width: 1170px;\n  margin: auto;\n}\nimg {\n  max-width: 100%;\n}\n.inbox_people {\n  background: #f8f8f8 none repeat scroll 0 0;\n  float: left;\n  overflow: hidden;\n  width: 40%;\n  border-right: 1px solid #c4c4c4;\n}\n.inbox_msg {\n  border: 1px solid #c4c4c4;\n  clear: both;\n  overflow: hidden;\n}\n.top_spac {\n  margin: 20px 0 0;\n}\n\n.recent_heading {\n  float: left;\n  width: 40%;\n}\n.srch_bar {\n  display: inline-block;\n  text-align: right;\n  width: 60%;\n}\n.headind_srch {\n  padding: 10px 29px 10px 20px;\n  overflow: hidden;\n  border-bottom: 1px solid #c4c4c4;\n}\n\n.recent_heading h4 {\n  color: #05728f;\n  font-size: 21px;\n  margin: auto;\n}\n.srch_bar input {\n  border: 1px solid #cdcdcd;\n  border-width: 0 0 1px 0;\n  width: 80%;\n  padding: 2px 0 4px 6px;\n  background: none;\n}\n.srch_bar .input-group-addon button {\n  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;\n  border: medium none;\n  padding: 0;\n  color: #707070;\n  font-size: 18px;\n}\n.srch_bar .input-group-addon {\n  margin: 0 0 0 -27px;\n}\n\n.chat_ib h5 {\n  font-size: 15px;\n  color: #464646;\n  margin: 0 0 8px 0;\n}\n.chat_ib h5 span {\n  font-size: 13px;\n  float: right;\n}\n.chat_ib p {\n  font-size: 14px;\n  color: #989898;\n  margin: auto;\n}\n.chat_img {\n  float: left;\n  width: 11%;\n}\n.chat_ib {\n  float: left;\n  padding: 0 0 0 15px;\n  width: 88%;\n}\n\n.chat_people {\n  overflow: hidden;\n  clear: both;\n}\n.chat_list {\n  border-bottom: 1px solid #c4c4c4;\n  margin: 0;\n  padding: 18px 16px 10px;\n}\n.inbox_chat {\n  height: 550px;\n  overflow-y: scroll;\n}\n\n.active_chat {\n  background: #ebebeb;\n}\n\n.incoming_msg_img {\n  display: inline-block;\n  width: 6%;\n}\n.received_msg {\n  display: inline-block;\n  padding: 0 0 0 10px;\n  vertical-align: top;\n  width: 92%;\n}\n.received_withd_msg p {\n  background: #ebebeb none repeat scroll 0 0;\n  border-radius: 3px;\n  color: #646464;\n  font-size: 14px;\n  margin: 0;\n  padding: 5px 10px 5px 12px;\n  width: 100%;\n}\n.time_date {\n  color: #747474;\n  display: block;\n  font-size: 12px;\n  margin: 8px 0 0;\n}\n.received_withd_msg {\n  width: 57%;\n}\n.mesgs {\n  float: left;\n  padding: 30px 15px 0 25px;\n  width: 60%;\n}\n\n.sent_msg p {\n  background: #05728f none repeat scroll 0 0;\n  border-radius: 3px;\n  font-size: 14px;\n  margin: 0;\n  color: #fff;\n  padding: 5px 10px 5px 12px;\n  width: 100%;\n}\n.outgoing_msg {\n  overflow: hidden;\n  margin: 26px 0 26px;\n}\n.sent_msg {\n  float: right;\n  width: 46%;\n}\n.input_msg_write input {\n  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;\n  border: medium none;\n  color: #4c4c4c;\n  font-size: 15px;\n  min-height: 48px;\n  width: 100%;\n}\n\n.type_msg {\n  border-top: 1px solid #c4c4c4;\n  position: relative;\n}\n.msg_send_btn {\n  background: #05728f none repeat scroll 0 0;\n  border: medium none;\n  border-radius: 50%;\n  color: #fff;\n  cursor: pointer;\n  font-size: 17px;\n  height: 33px;\n  position: absolute;\n  right: 0;\n  top: 11px;\n  width: 33px;\n}\n.messaging {\n  padding: 0 0 50px 0;\n}\n.msg_history {\n  height: 516px;\n  overflow-y: auto;\n}\n</style>\n"],"sourceRoot":""}]);
